<template>
  <vs-popup fullscreen :title="$t('LOCALIZATIONS')" :active.sync="activeInternal">
    <div :ref="popupRef" class="vs-con-loading__container">
      <div class="alert-container" v-if="alertActive">
        <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
          <p>{{ alertMessage }}</p>
        </vs-alert>
      </div>

      <div class="inline-form p-4 mb-base" v-if="localizationFormVisible">
        <div class="nv-row mb-4">
          <div class="nv-col w-full">
            <vs-select autocomplete class="w-full" :label="$t('LANGUAGE')" :placeholder="$t('SELECT_VALUE')" v-model="locale">
              <vs-select-item v-for="(language, index) in languages" :key="index" :value="language.locale" :text="language.languageName"/>
            </vs-select>
          </div>
        </div>
        <div class="nv-row mb-4">
          <div v-if="!textarea" class="nv-col w-full">
            <vs-input class="w-full" :label="$t('LOCALIZED')" v-model="localized"/>
          </div>
          <div v-else class="nv-col w-full">
            <label class="vs-input--label">{{ $t('DESCRIPTION') }}</label>
            <vs-textarea class="w-full" v-model="description"/>
          </div>
        </div>
        <div class="nv-row">
          <div class="nv-col w-full">
            <div class="flex justify-between">
              <vs-button class="px-5 py-3" color="primary" type="border" @click="hideLocalizationForm">{{ $t('CANCEL') }}</vs-button>
              <vs-button class="px-5 py-3" @click="createLocalizedEntityField">{{ $t('SUBMIT') }}</vs-button>
            </div>
          </div>
        </div>
      </div>

      <vs-table class="mb-base" :data="localizationTableData" :noDataText="localizationTableNoDataText" :sst="true" search @search="filterLocalizationTable" @sort="orderLocalizationTable">
        <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
          <div class="flex flex-wrap-reverse items-center">
            <vs-button class="p-3 mr-4" @click="showLocalizationForm" :disabled="localizationFormVisible" color="primary" type="filled" icon="fa-plus" icon-pack="far">{{ $t('ADD') }}</vs-button>
            <vs-button class="p-3 mr-4" @click="refreshLocalizationTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="localizationTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>

          <div class="flex justify-end mr-4">
            <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
              <span class="mx-2">{{ localizationTablePagingInfo }}</span>
              <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
                <span class="far fa-angle-down text-primary"></span>
                <vs-dropdown-menu>
                  <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="localizationTablePerPage = item">
                    <span>{{ item }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>

        <template slot="thead">
          <vs-th sort-key="locale">
            {{ this.$t('LANGUAGE') }}
          </vs-th>
          <vs-th sort-key="localized">
            {{ this.$t('LOCALIZED') }}
          </vs-th>
          <vs-th>
            {{ this.$t('OPTIONS') }}
          </vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
            <vs-td :data="data[index].locale">
              {{ data[index].locale }}
            </vs-td>
            <vs-td :data="data[index].localized">
              {{ data[index].localized }}
            </vs-td>
            <vs-td :data="data">
              <vs-button class="p-3 mr-4" color="danger" @click="deleteLocalizedEntityField(data[index])" icon-pack="far" icon="fa-trash-alt">{{ $t('DELETE') }}</vs-button>
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>

      <vs-pagination :total="localizationTableTotalPages" v-model="localizationTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
    </div>
  </vs-popup>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import PopupMixin from '@/mixins/commons/PopupMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import LocalizedEntityFieldService from '@/services/commons/LocalizedEntityFieldService'
import LanguageService from '@/services/commons/LanguageService'

export default {
  name: 'nv-localization-popup',
  mixins: [AlertMixin, LoadingMixin, ListLayoutMixin, PopupMixin],
  props: {
    path: {
      type: String,
      default: null,
      required: true
    },
    fieldName: {
      type: String,
      default: null,
      required: true
    },
    pkValue: {
      type: String,
      default: null,
      required: true
    },
    textarea: {
      type: Boolean,
      default: false
    },
    defaultLocalizationTableFilter: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      localizationTableData: [],
      localizationTableFilter: this.defaultLocalizationTableFilter,
      localizationTableFilterTimer: null,
      localizationTableOrderBy: null,
      localizationTableContainer: this.popupRef,
      localizationTableBusy: false,
      localizationTableTotalRows: 0,
      localizationTableCurrentPage: 1,
      localizationTablePerPage: 10,
      localizationFormVisible: false,
      locale: 'en',
      languages: [],
      localized: ''
    }
  },
  computed: {
    localizationTableNoDataText: function () {
      return this.localizationTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    localizationTableTotalPages: function () {
      return Math.ceil(this.localizationTableTotalRows / this.localizationTablePerPage)
    },
    localizationTablePagingInfo: function () {
      return this.pagingInfo(this.localizationTableCurrentPage, this.localizationTablePerPage, this.localizationTableTotalRows)
    }
  },
  methods: {
    async load () {
      let self = this
      LanguageService.languages({
        disabled: 'N'
      }).then(function (response) {
        self.languages = response.data.languageList || []
      })
    },
    loadLocalizationTableData (loadingContainerId) {
      let self = this
      self.localizationTableBusy = true
      this.localizationTableContainer = loadingContainerId || this.localizationTableContainer
      this.startLoading(this.localizationTableContainer)
      return LocalizedEntityFieldService.localizedEntityFields(this.path, {
        pageIndex: this.localizationTableCurrentPage - 1,
        pageSize: this.localizationTablePerPage,
        filter: this.localizationTableFilter,
        orderByField: this.localizationTableOrderBy,
        fieldName: this.fieldName,
        pkValue: this.pkValue
      }).then(function (response) {
        self.stopLoading(self.localizationTableContainer)
        self.localizationTableBusy = false
        self.localizationTableTotalRows = response.data.totalRows
        self.localizationTableData = response.data.localizedEntityFieldList || []
      })
    },
    refreshLocalizationTable () {
      this.loadLocalizationTableData()
    },
    filterLocalizationTable (filter) {
      let self = this
      if (this.localizationTableFilter !== filter) {
        clearTimeout(this.localizationTableFilterTimer)
        this.localizationTableFilterTimer = setTimeout(() => {
          self.localizationTableFilter = filter
          self.loadLocalizationTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.localizationTableFilterTimer)
      }
    },
    orderLocalizationTable (orderBy, active) {
      this.localizationTableOrderBy = (active ? '' : '-') + orderBy
      this.loadLocalizationTableData()
    },
    createLocalizedEntityField () {
      let self = this
      LocalizedEntityFieldService.createLocalizedEntityField(this.path, {
        locale: this.locale,
        localized: this.localized,
        fieldName: this.fieldName,
        pkValue: this.pkValue
      }).then(function (response) {
        self.refreshLocalizationTable()
        self.hideLocalizationForm()
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    deleteLocalizedEntityField (item) {
      let self = this
      LocalizedEntityFieldService.deleteLocalizedEntityField(this.path, {
        locale: item.locale,
        fieldName: item.fieldName,
        pkValue: item.pkValue
      }).then(function (response) {
        if (response.status === 200) {
          self.refreshLocalizationTable()
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    showLocalizationForm () {
      this.localizationFormVisible = true
    },
    hideLocalizationForm () {
      this.localizationFormVisible = false
    }
  },
  watch: {
    localizationTableCurrentPage: function () {
      this.refreshLocalizationTable()
    },
    localizationTablePerPage: function () {
      this.refreshLocalizationTable()
    }
  },
  mounted () {
    this.load()
    this.loadLocalizationTableData()
  },
  beforeDestroy: function () {
    this.$el.remove()
  }
}
</script>
