import Endpoint from '@/services/Endpoint'

export default {
  localizedEntityFields (path, payload) {
    return Endpoint().get(path, {
      params: payload
    })
  },
  createLocalizedEntityField (path, payload) {
    return Endpoint().post(path, payload)
  },
  deleteLocalizedEntityField (path, payload) {
    return Endpoint().delete(path, {
      params: payload
    })
  }
}
