<template>
  <nv-card>
    <div class="flex flex-wrap-reverse items-center mb-base">
      <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
      <vs-button class="p-3 mr-4" @click="modify" color="primary" type="filled" icon="fa-pen" icon-pack="far" v-if="$acl.check('isTradeFinanceAll')">{{ $t('MODIFY') }}</vs-button>
    </div>
    <div class="row hover">
      <p class="info-row">
        <span>{{ $t('ID') }}:</span>
        <span>{{ dcType.typeId }}</span>
      </p>
      <p class="info-row">
        <span>{{ $t('TYPE_NAME') }}:</span>
        <span>
          <nv-localization-popup :active="popupActive" v-on:close="popupActive = false" path="/s1/tradeFinance/dcType/localization" popupRef="localizationModal" :pkValue="this.$route.params.id" fieldName="typeName"/>
          <vs-button class="m-0 p-0" color="primary" type="flat" @click="popupActive = true"><i class="far fa-globe-africa mr-1 text-primary"></i></vs-button>
          {{ dcType.typeName }}
        </span>
      </p>
      <p class="info-row">
        <span>{{ $t('TYPE_BEHAVIOUR') }}:</span>
        <span>{{ dcType.typeBehaviourDescription }}</span>
      </p>
      <p class="info-row">
        <span>{{ $t('EXTERNAL_ID') }}:</span>
        <span>{{ dcType.externalTypeId | placeholder('-') }}</span>
      </p>
      <p class="info-row">
        <span>{{ $t('CREATION_DATE') }}:</span>
        <span>{{ dcType.creationDate | formatUnixTimestamp }}</span>
      </p>
    </div>
  </nv-card>
</template>

<script>
import DcTypeService from '@/services/trade/DcTypeService'
import NvLocalizationPopup from '@/components/nv-localization/NvLocalizationPopup'

export default {
  name: 'dc-type-view',
  components: {
    NvLocalizationPopup
  },
  data: function () {
    return {
      dcType: {},
      popupActive: false
    }
  },
  methods: {
    async load () {
      let self = this
      DcTypeService.dcTypes({
        typeId: this.$route.params.id
      }).then(function (response) {
        self.dcType = response.data.dcTypeList[0] || {}
      })
    },
    cancel () {
      this.$router.back()
    },
    modify () {
      this.$router.push({ name: 'ModifyDcType', params: { id: this.$route.params.id } })
    }
  },
  mounted () {
    this.load()
  }
}
</script>
