let PopupMixin = {
  props: {
    popupRef: {
      type: String,
      default: 'popup'
    },
    active: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      activeInternal: false
    }
  },
  methods: {
    selectRow (row) {
      this.$emit('select-row', row)
      this.activeInternal = false
    }
  },
  watch: {
    active: function () {
      this.activeInternal = this.active
    },
    activeInternal: function () {
      if (!this.activeInternal) {
        this.$emit('close')
      }
    }
  }
}

export default PopupMixin
